<template>
	<div id="container">
		<div class="top">
			<el-button type="primary" @click="addObj.flag=true">添加游戏</el-button>
		</div>
		<el-table 
			stripe
			:data="tableData.filter(data => !search || String(data.phone).toLowerCase().includes(search.toLowerCase()))" 
			style="width: 100%"
			max-height='730px'
			v-loading="loading"
			>
			<el-table-column prop="shopinfo[0].name" label="门店名"> </el-table-column>
			<el-table-column prop="gamelist[0].name" label="游戏名"> </el-table-column>
			<el-table-column prop="money" label="游戏定价(元)"> </el-table-column>
			<el-table-column prop="discount" label="折扣(%)"> </el-table-column>
			<el-table-column prop="divide" label="评分(%)"> </el-table-column>
			<el-table-column fixed="right" label="操作">
			  <template slot-scope="scope">
				<el-button size="mini" @click="handleEdit(scope.$index, scope.row);changeObj.flag = true">编辑</el-button>
				<el-button size="mini" @click.native.prevent="handleDelete(scope.$index, scope.row)">移除</el-button>
			  </template>
			</el-table-column>
		</el-table>

		<!-- 添加 -->
		<el-dialog title="添加游戏" :visible.sync="addObj.flag" width="50%" center>
			<el-form label-position="left" label-width="100px">
				<el-form-item label="门店名:">
					 <el-select v-model="addObj.m_id" filterable placeholder="选择您要添加游戏的门店">
					    <el-option
					      v-for="item in userList"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					  </el-select>
				</el-form-item>
				<el-form-item label="游戏名:">
					 <el-select v-model="addObj.game_id" filterable placeholder="选择您要添加的游戏">
					    <el-option
					      v-for="item in gameList"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					  </el-select>
				</el-form-item>
				<el-form-item label="游戏定价(元):">
					<el-input v-model="addObj.money" type="number" size="small"></el-input>
				</el-form-item>
				<el-form-item label="折扣(%):">
					<el-input v-model="addObj.discount" type="number" size="small"></el-input>
				</el-form-item>
				<el-form-item label="评分(%):">
					<el-input v-model="addObj.divide" type="number" size="small"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="addclear()">取 消</el-button>
				<el-button type="primary" @click="addTrue()">确 定</el-button>
			</div>
		</el-dialog>

		<!-- 编辑 -->
		<el-dialog title="编辑信息" :visible.sync="changeObj.flag" width="50%" center>
			<el-form label-position="left" label-width="100px">
				<el-form-item label="门店名:">
					<el-input v-model="changeObj.m_id" type="text" size="small" disabled></el-input>
				</el-form-item>
				<el-form-item label="游戏名:">
					<el-input v-model="changeObj.game_id" type="text" size="small" disabled></el-input>
				</el-form-item>
				<el-form-item label="游戏定价(元):">
					<el-input v-model="changeObj.money" type="number" size="small"></el-input>
				</el-form-item>
				<el-form-item label="折扣(%):">
					<el-input v-model="changeObj.discount" type="number" size="small"></el-input>
				</el-form-item>
				<el-form-item label="评分(%):">
					<el-input v-model="changeObj.divide" type="number" size="small"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="changeObj.flag=false">取 消</el-button>
				<el-button type="primary" @click="changeTrue">确 定</el-button>
			</div>
		</el-dialog>

	<el-pagination
		v-if="tableData"
		background
		layout="sizes, prev, pager, next, jumper"
		:total="fenye.total"
		:page-sizes="[10, 20, 30, 50]"
		@current-change="yebian"
		@size-change='tiaobian'
		:page-size="fenye.page_size"
		:current-page="fenye.current_page">
	</el-pagination>
	</div>
</template>

<script>

 export default {
	 //将每组需要操作的数据，分成每个对象，在方法里进行再次的处理
	data() {
		return {
			//公用部分
			tableData: [{name:'sad'}],//列表的数据
			userList:[],//用户的列表信息
			gameList:[],//游戏的列表信息
			space:'',//地址的数据（从vuex里获得）
			loading:true,
			//
			search:'',//搜索用
			top_sou:'',
			addObj:{//添加用
				game_id:'',
				money:'',
				discount:'',
				divide:'',
				m_id:'',
				flag:false
			},
			fenye:{//分页用
				total:1,//总条数
				page_size:10,//单页条数
				current_page:1,//页码
			},
			changeObj:{//编辑用
				flag:false,
				game_id:'',
				money:'',
				discount:'',
				divide:'',
				m_id:'',
				id:''
			}
		}
	},
	methods:{
		//编辑按钮
		handleEdit(index, row) {
			console.log(this.tableData)
			this.changeObj.game_id = this.tableData[index].game_id
			this.changeObj.money = this.tableData[index].money
			this.changeObj.discount = this.tableData[index].discount
			this.changeObj.divide = this.tableData[index].divide
			this.changeObj.m_id = this.tableData[index].m_id
			this.changeObj.id = this.tableData[index].id
			console.log(this.changeObj)
		},
		//编辑确定
		changeTrue(){
			this.loading = true
			let that = this
			let obj = {
				token:this.$store.state.userInfo.token,
				name:this.$store.state.userInfo.name,
				discount:this.changeObj.discount,
				divide:this.changeObj.divide,
				money:this.changeObj.money,
				id:this.changeObj.id
			}
			this.$axios.post("/shopgamepriceEdit",obj).then(function(res){
				if(res.data.code == '1'){
					that.$message({
						type: 'info',
						message: res.data.message,
					}); 
				}else{
					that.changeObj.flag=false
					that.shebeiList(that.fenye.current_page,that.fenye.page_size)
					that.$message({
						type: 'success',
						message: res.data.message
					});
				}
			})
		},
		//删除按钮
		handleDelete(index, row) {
			this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				this.tableData.splice(index,1)
				this.$message({
					type: 'success',
					message: '删除成功!'
				});
			}).catch(() => {
				this.$message({
					type: 'info',
					message: '已取消删除'
				});          
			});
		},
		//分页
		yebian(currentPage){//页面的页码发生变化的时候
			this.loading = true	
			this.fenye.current_page = currentPage
			this.shebeiList(currentPage,this.fenye.page_size)
		},
		tiaobian(ind){//页面的条数发生变化的时候
			this.loading = true
			this.fenye.page_size = ind
			this.shebeiList(this.fenye.current_page,ind)
		},
		//添加游戏的确定按钮
		addTrue(){
			this.loading = true
			//下面是处理数据，传给后台
			let that = this
			let obj = {
				'name':this.$store.state.userInfo.name,
				'token':this.$store.state.userInfo.token,
				'm_id':this.addObj.m_id,
				'game_id':this.addObj.game_id,
				'money':this.addObj.money,
				'discount':this.addObj.discount,
				'divide':this.addObj.divide
			}
			//提交申请
			this.$axios.post("/shopgamepriceAdd",obj).then(function(res){
				//创建失败
				if(res.data.code == '1'){
					that.$message({
						type: 'info',
						message: res.data.message,
					}); 
				}else{
				//创建成功
					that.$message({
						type: 'success',
						message: res.data.message
					});
				}
				that.shebeiList(that.fenye.current_page,that.fenye.page_size)
				that.addclear()
			})
		},
		//在添加用户完成或取消，消除内容
		addclear(){
			this.addObj.m_id = ''
			this.addObj.game_id = ''
			this.addObj.money = ''
			this.addObj.discount = ''
			this.addObj.divide = ''
			this.addObj.flag = false
		},
		//门店游戏列表
		shebeiList(ind,num){
			let that = this
			let name = this.$store.state.userInfo.name
			let token = this.$store.state.userInfo.token
			let obj = {
				'name':this.$store.state.userInfo.name,
				'token':this.$store.state.userInfo.token,
				'page':ind,
				'pageSize':num
			}
			this.$axios.post("/shopgameprice",obj).then(function(res){
				that.fenye.total = res.data.pageCount * res.data.pageSize
				that.tableData = res.data.data
				console.log(that.tableData)
				that.loading = false
			})
		}
	},
	//挂载完成执行的生命钩子函数
	created () {
		//获得三级菜单，处理数据
		this.space = this.$store.state.space
		//获得门店主列表信息
		let that = this
		let obj = {
			'name':this.$store.state.userInfo.name,
			'token':this.$store.state.userInfo.token
		}
		//门店设备列表的数据
		this.shebeiList(this.fenye.current_page,this.fenye.page_size)
	}
}
</script>



<style scoped lang="less">


#container{
  padding-right: 10px;
}
.top{
  width:100%;
  height: 60px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.el-table-column label{
	text-align: center;
}
</style>
